

.border {
    margin-left: auto;
    margin-right: auto;
    /* margin-bottom: 18px; */
    padding-bottom: 16px;
    width: 130px;
    border-bottom: solid rgba(0, 0, 0, 0.15) 1px ;
    }
@media screen and (min-width: 768px) {
    
   .border { width: 330px;
padding-bottom: 32px;
/* margin-bottom: 40px; */

}
 }

.description {
        font-style: normal;
        font-weight: 500;
        font-size: 10px;
        line-height: 1.2;
        text-align: center;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: black;
}
@media screen and (min-width: 768px) {
    .description {
        font-size: 14px;
    }
 }

 .rectangle {
    display: inline-block;
   margin-right: 6px;
    width: 14px;
    height: 14px;
 }

 @media screen and (min-width: 768px) {
     .rectangle {
        width: 25px;
            height: 25px;
     }
 }

 .answers {
    display: flex;
    justify-content: right;
    align-items: center;
    margin-bottom: 14px;
    margin-right: 10px;
        font-size: 8px;
        line-height: 1.12;
    
        letter-spacing: 0.02em;
 }
  @media screen and (min-width: 768px) {
      .answers {
        margin-right: 22px;
        margin-bottom: 37px;
         font-size: 16px;
         line-height: 1.18;
      }
  }


