.list {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    align-items: center;
}
@media screen and (min-width: 768px) {
    .list {
        margin-bottom: 32px;
    }
}
@media screen and (min-width: 1280px) {
    .list {
        margin-bottom: 28px;
    }
}

